export function getLineBreak(lineBreakType) {
    switch (lineBreakType) {
        case "CR":
            return "\r";
        case "LF":
            return "\n";
        case "CRLF":
            return "\r\n";
    }
}
export function getLineBreakType(value) {
    if (isLineBreakType(value)) {
        return value;
    }
    else {
        return "LF";
    }
}
function isLineBreakType(value) {
    return value === "CR" || value === "LF" || value === "CRLF";
}
